@charset "UTF-8";

/******************************* Global *******************************/

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  font-family: "Vazirmatn FD" !important;
}

/******************************* Timeline *******************************/

.vco-slider {
  .slider-item .content .content-container {
    .media-shadow {
      &:before,
      &:after {
        background: none !important;
        box-shadow: none !important;
      }
    }

    .media .media-wrapper {
      .media-container .media-image img {
        border: none !important;
        border-radius: 20px;
        transition: all 0.5s;

        &:hover,
        &:active {
          transform: scale(1.02);
        }
      }

      .caption {
        font-family: Vazirmatn FD;
        font-weight: 200;
        text-align: center !important;
      }
    }
  }

  .nav-next {
    transition: all 0.25s;

    .date,
    .title {
      direction: rtl;
      font-family: "Vazirmatn FD" !important;
    }

    .icon {
      transition: all 0.25s;
    }
  }

  .nav-previous {
    transition: all 0.25s;

    .date,
    .title {
      direction: rtl;
      font-family: "Vazirmatn FD" !important;
    }

    .icon {
      transition: all 0.25s;
    }
  }
}

.vco-storyjs {
  .vco-feature {
    h3,
    h4,
    h5,
    h6 {
      margin-bottom: 15px;
      margin-top: 15px;
    }
  }

  h2.date,
  h3 {
    text-align: right;
    direction: rtl;
  }

  p {
    text-align: justify;
    text-align-last: right;
    direction: rtl;
  }
}

.vco-timeline .vco-navigation {
  .timenav-background .timenav-line {
    background-color: #f34235 !important;
  }

  .timenav .content .marker.active {
    .dot,
    .line {
      background: #f34235 !important;
    }

    .flag .flag-content h3 {
      color: #f34235 !important;
    }
  }

  .vco-toolbar {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border: none !important;
    box-shadow: 2px 0px 2px rgba(0, 0, 0, 0.1) !important;

    .back-home {
      border-top-right-radius: 5px;
      transition: all 0.25s;
    }

    .zoom-in {
      transition: all 0.25s;
    }

    .zoom-out {
      border-bottom-right-radius: 5px;
      transition: all 0.25s;
    }
  }
}

/******************************* Wikipedia *******************************/

.wikipedia {
  h4 {
    text-align: right;
    direction: rtl;
    border-bottom: none !important;
    margin-bottom: 10px !important;
  }

  .wiki-source {
    display: block !important;
    text-align: right;
    direction: rtl;
    margin-bottom: 10px !important;
    font-family: "Vazirmatn FD" !important;
    color: gray;
    font-weight: 100;
  }
}

/******************************* Tooltip *******************************/

.tooltip {
  font-family: "Vazirmatn FD" !important;
  font-weight: 300;
}

/******************************* Footer *******************************/

.footer {
  font-size: 10px;
  text-align: center;

  a {
    width: 100px;
    margin: 5px 15px 0 15px;
    padding: 2px 5px 2px 5px;
    display: inline-block;
    background-color: transparent;
    border: 1px solid #f34235;
    border-radius: 5px;
    text-decoration: none;
    color: #f34235;
    font-weight: normal;
    font-size: 12px;
    transition: all 0.2s;

    &:hover {
      background-color: #f34235;
      color: white;
      font-weight: bold;
    }

    &:active {
      background-color: #f34235;
      color: white;
      font-weight: bold;
    }
  }
}
